:root {
  --ui-container: var(--container-8xl);
  --gradient-accent: linear-gradient(135deg, #3b82f6 0%, #06b6d4 100%);
}
*,
*::before,
*::after {
  box-sizing: border-box;
}
body, h1, h2, h3, h4, h5, h6, p, ul, ol, li {
  margin: 0;
  padding: 0;
}
[class*="mx-auto"] {
  margin-left: auto !important;
  margin-right: auto !important;
}
html {
  scroll-behavior: smooth;
}
body {
  background: var(--color-dark);
  color: var(--color-text);
  font-family: var(--font-sans);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.hero-background {
  background: var(--color-dark);
  position: relative;
  overflow: hidden;
}
@keyframes pcb-gradient-float {
  0%, 100% {
    transform: translate(0, 0) scale(1);
    opacity: 0.3;
  }
  50% {
    transform: translate(20px, -20px) scale(1.05);
    opacity: 0.5;
  }
}
.pcb-gradient-float {
  animation: pcb-gradient-float 8s ease-in-out infinite;
}
.gradient-text {
  background: var(--gradient-accent);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}
@keyframes brand-gradient-shift {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}
.brand-title {
  position: relative;
  letter-spacing: 0.08em;
  text-transform: uppercase;
}
.brand-title.gradient-text {
  background-image: linear-gradient(
    120deg,
    var(--color-green-300),
    var(--color-green-400),
    var(--color-green-300)
  );
  background-size: 200% 200%;
  animation: brand-gradient-shift 10s ease-in-out infinite;
}
.brand-title::after {
  content: '';
  position: absolute;
  left: 0;
  bottom: -0.35rem;
  width: 100%;
  height: 2px;
  border-radius: 999px;
  background: radial-gradient(circle at 0% 50%, rgba(59, 130, 246, 0.0), rgba(59, 130, 246, 0.5), rgba(6, 182, 212, 0.0));
  transform-origin: left;
  transform: scaleX(0);
  transition: transform 0.4s ease-out;
}
.brand-title:hover::after {
  transform: scaleX(1);
}
.brand-title-hover {
  transition: transform 0.4s ease, text-shadow 0.4s ease;
}
.brand-title-hover:hover {
  transform: translateY(-1px) scale(1.02);
  text-shadow: 0 0 12px rgba(59, 130, 246, 0.6),
    0 0 24px rgba(6, 182, 212, 0.35);
}
.prose {
  color: var(--color-text);
  max-width: 65ch;
}
.prose h1 {
  font-size: 2.5rem;
  font-weight: 700;
  line-height: 1.2;
  margin-top: 0;
  margin-bottom: 1.5rem;
  color: var(--color-primary);
  letter-spacing: -0.02em;
}
.prose h2 {
  font-size: 1.875rem;
  font-weight: 600;
  line-height: 1.3;
  margin-top: 3rem;
  margin-bottom: 1rem;
  color: var(--color-primary);
  padding-top: 1rem;
  border-top: 1px solid var(--color-border);
  scroll-margin-top: 4rem;
}
.prose h2:first-of-type {
  border-top: none;
  padding-top: 0;
  margin-top: 0;
}
.prose h3 {
  font-size: 1.5rem;
  font-weight: 600;
  line-height: 1.4;
  margin-top: 2rem;
  margin-bottom: 0.75rem;
  color: var(--color-text);
}
.prose h4 {
  font-size: 1.25rem;
  font-weight: 600;
  line-height: 1.4;
  margin-top: 1.5rem;
  margin-bottom: 0.5rem;
  color: var(--color-text);
}
.prose p {
  margin-bottom: 1.25rem;
  color: var(--color-text-muted);
  font-size: 1rem;
  line-height: 1.75;
}
.prose ul,
.prose ol {
  margin-bottom: 1.5rem;
  padding-left: 1.75rem;
}
.prose li {
  margin-bottom: 0.5rem;
  color: var(--color-text-muted);
  line-height: 1.75;
}
.prose code {
  background: var(--color-dark-alt);
  color: #f87171;
  padding: 0.125rem 0.375rem;
  border-radius: 0.25rem;
  font-size: 0.875em;
  font-family: 'SF Mono', 'Monaco', 'Inconsolata', 'Fira Code', monospace;
  border: 1px solid var(--color-border);
}
.prose pre {
  background: var(--color-dark-alt);
  color: #e2e8f0;
  padding: 0;
  border-radius: 0.5rem;
  overflow-x: auto;
  margin: 1.5rem 0;
  font-size: 0.875rem;
  line-height: 1.6;
  font-family: 'SF Mono', 'Monaco', 'Inconsolata', 'Fira Code', 'Droid Sans Mono', 'Source Code Pro', monospace;
  border: 1px solid var(--color-border);
}
.prose pre code {
  background: transparent;
  color: inherit;
  padding: 0;
  border: none;
  font-size: inherit;
}
.prose blockquote {
  border-left: 4px solid var(--color-accent);
  padding-left: 1.5rem;
  margin: 1.5rem 0;
  color: var(--color-text-muted);
  font-style: italic;
  background: var(--color-dark-alt);
  padding: 1rem 1.5rem;
  border-radius: 0.375rem;
}
.prose a {
  color: var(--color-green-400) !important;
  text-decoration: none;
  border-bottom: 1px solid transparent;
  transition: all 0.2s;
}
.prose a:hover {
  color: var(--color-green-300) !important;
  border-bottom-color: var(--color-green-400);
}
.prose hr {
  border: none;
  border-top: 1px solid var(--color-border);
  margin: 2rem 0;
}
.prose table {
  width: 100%;
  border-collapse: collapse;
  margin: 1.5rem 0;
  font-size: 0.9375rem;
}
.prose th {
  background: var(--color-dark-alt);
  font-weight: 600;
  text-align: left;
  padding: 0.75rem 1rem;
  border-bottom: 2px solid var(--color-border);
  color: var(--color-primary);
}
.prose td {
  padding: 0.75rem 1rem;
  border-bottom: 1px solid var(--color-border);
  color: var(--color-text-muted);
}
.prose tr:hover {
  background: var(--color-dark-alt);
}
pre code {
  display: block;
  width: 100%;
  background: transparent !important;
  color: inherit;
}
pre code .line {
  display: block;
  min-height: 1.5rem;
}
pre code span {
  display: inline;
}
pre code .line.highlighted {
  background-color: rgba(59, 130, 246, 0.1);
  display: block;
  margin-left: -1rem;
  margin-right: -1rem;
  padding-left: 1rem;
  padding-right: 1rem;
}
pre code span.word {
  background-color: rgba(59, 130, 246, 0.2);
  padding: 0.125rem 0.25rem;
  border-radius: 0.25rem;
}
.logo-hover-green {
  filter: brightness(0) invert(1);
  transition: filter 0.3s ease;
}
.logo-hover-green:hover {
  filter: brightness(0) invert(1) sepia(1) saturate(4) hue-rotate(130deg) brightness(1.1);
}
nextjs-portal:empty {
  display: none !important;
  visibility: hidden !important;
  pointer-events: none !important;
}
nextjs-portal {
  position: fixed;
  z-index: -1;
}
nextjs-portal:not(:empty) {
  position: fixed;
